input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  box-sizing: border-box;
  border: 1px solid #999;
  padding: 0.4em 0.7em; }
  input[type='text']:focus,
  input[type='tel']:focus,
  input[type='email']:focus,
  input[type='search']:focus,
  input[type='password']:focus,
  textarea:focus {
    border-color: #333; }
  input[type='text']::-webkit-input-placeholder,
  input[type='tel']::-webkit-input-placeholder,
  input[type='email']::-webkit-input-placeholder,
  input[type='search']::-webkit-input-placeholder,
  input[type='password']::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder {
    color: #999; }
  input[type='text']::-moz-placeholder,
  input[type='tel']::-moz-placeholder,
  input[type='email']::-moz-placeholder,
  input[type='search']::-moz-placeholder,
  input[type='password']::-moz-placeholder,
  textarea::-moz-placeholder {
    opacity: 1;
    color: #999; }
  input[type='text']:-moz-placeholder,
  input[type='tel']:-moz-placeholder,
  input[type='email']:-moz-placeholder,
  input[type='search']:-moz-placeholder,
  input[type='password']:-moz-placeholder,
  textarea:-moz-placeholder {
    color: #999; }
  input[type='text']:-ms-input-placeholder,
  input[type='tel']:-ms-input-placeholder,
  input[type='email']:-ms-input-placeholder,
  input[type='search']:-ms-input-placeholder,
  input[type='password']:-ms-input-placeholder,
  textarea:-ms-input-placeholder {
    color: #999; }
  input[type='text'].placeholder,
  input[type='tel'].placeholder,
  input[type='email'].placeholder,
  input[type='search'].placeholder,
  input[type='password'].placeholder,
  textarea.placeholder {
    color: #999; }

select {
  -webkit-border-radius: 0; }

textarea {
  resize: vertical;
  vertical-align: top; }

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer; }

.wrapper {
  padding: 20px 15px;
  overflow: hidden; }

.caption-holder {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-top: 20px; }
  @media only screen and (min-width: 756px) {
    .caption-holder {
      max-width: 400px;
      padding-bottom: 20px; } }
  .caption-holder h1 {
    text-transform: uppercase;
    letter-spacing: 0.1em; }
    @media screen and (min-width: 756px) {
      .caption-holder h1 {
        font-size: 35px; } }
  .caption-holder p {
    color: #02a388;
    font-size: 20px;
    letter-spacing: 0.1em; }

.filter-holder {
  width: 100%; }
  @media screen and (max-width: 756px) {
    .filter-holder {
      overflow-x: auto;
      flex-wrap: nowrap;
      padding: 10px 0; } }

.filter-by-letter {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0; }
  @media screen and (max-width: 756px) {
    .filter-by-letter {
      flex-wrap: nowrap;
      padding: 10px 0;
      width: 100%; } }
  @media screen and (min-width: 756px) {
    .filter-by-letter {
      justify-content: center; } }
  .filter-by-letter li {
    padding: 5px;
    list-style-type: none; }
    .filter-by-letter li:not(:last-child) {
      margin-right: 10px; }
    .filter-by-letter li.active a {
      color: #fff;
      background-color: #02a388; }
  .filter-by-letter a {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    text-decoration: none;
    transition: 0.3s background-color ease-in-out; }
    .filter-by-letter a:hover {
      color: #fff;
      background-color: #02a388; }

.movie-item:nth-child(odd) td {
  background-color: lavender; }

.search-result {
  font-size: 18px;
  font-weight: 700;
  padding: 0;
  margin: 0;
  justify-content: center; }
  @media screen and (min-width: 756px) {
    .search-result {
      display: flex;
      justify-content: center; } }
  .search-result li {
    list-style-type: none;
    margin-bottom: 10px; }
    .search-result li:not(:last-child) {
      margin-right: 10px; }

.search-count {
  color: #fe423f; }

.total-count {
  color: #02a388; }

.sort-movies {
  font-size: 15px;
  width: 180px; }

.movie-wrapper {
  position: relative;
  width: 100%; }
  @media only screen and (max-width: 756px) {
    .movie-wrapper {
      overflow-x: auto;
      margin: 0 auto; } }
  .movie-wrapper.not-found .not-found-msg {
    display: block; }

.not-found-msg {
  display: none;
  text-align: center; }

.movie-list {
  width: 100%;
  margin-bottom: 20px; }
  .movie-list th {
    background: #02a388;
    color: #fff;
    font-size: 1em;
    padding: 10px 12px;
    letter-spacing: 1px;
    text-transform: uppercase; }
    @media screen and (max-width: 1024px) {
      .movie-list th {
        font-size: 14px; } }
  .movie-list td {
    padding: 10px 12px;
